html {
  scroll-behavior: smooth;
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  scroll-behavior: smooth;
  font-family: "Montserrat", sans-serif;
  background-color: #121314;
  color: #efefef;
  font-size: 1.6rem;
}
body.body-fixed {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

p {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  line-height: 1.5;
}

a {
  font-family: "Bruno Ace SC", sans-serif;
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-family: "Bruno Ace SC", sans-serif;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  background: linear-gradient(262deg, rgb(24, 24, 24) 0%, rgb(19, 19, 19) 50%, rgb(0, 0, 0) 100%);
}
@media (min-width: 768px) {
  .header.fixed {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.1rem #1e2021;
  }
}
.header h1 {
  margin: 0;
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header h1:hover {
  transform: rotateY(360deg);
  transition: transform 2s;
}
.header .header-content {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .nav-main {
    display: flex;
    gap: 2rem;
  }
}
.nav-main a {
  display: block;
  text-align: center;
  color: #efefef;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav-main a {
    margin-bottom: 0;
  }
}
.nav-main a:hover {
  color: #cd9ce6;
  transform: scale(0.9, 1);
  transition: transform 0.5s ease;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: linear-gradient(95deg, rgb(12, 0, 43) 0%, rgba(131, 58, 205, 0.5800464037) 100%, rgba(63, 1, 98, 0.494199536) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.video-content {
  text-align: center;
  z-index: 1;
}
@media (min-width: 768px) {
  .video-content {
    text-align: left;
  }
}
.video-content h2 {
  font-size: 5rem;
  margin: 0;
}
.video-content p {
  font-family: "Bruno Ace SC", sans-serif;
  border-top-left-radius: 5rem;
  border-bottom-right-radius: 5rem;
  background-color: #652087;
  display: inline-block;
  padding: 1rem 2rem;
  font-weight: normal;
}

.about-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.image img {
  border-top-left-radius: 5rem;
  border-bottom-right-radius: 5rem;
}

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .festival-content {
    text-align: justify;
    margin-top: 0;
  }
}
.festival-content h2 {
  margin-bottom: 2rem;
}
.festival-content .date {
  color: #cd9ce6;
  font-weight: 700;
}

.webp .lineup {
  background-image: url(../img/concert.webp);
}

.no-webp .lineup {
  background-image: url(../img/concert.jpg);
}

.lineup {
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #cd9ce6;
  font-weight: bold;
}
.lineup .day {
  color: #efefef;
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .stage-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.stage-content p {
  color: #efefef;
}

.stage {
  border-radius: 1.5rem 1.5rem 1.5rem 1.5rem;
  padding-top: 2rem;
  margin-bottom: 2rem;
}
.stage p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 20rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
}

.rock .stage-name:after {
  background-image: url(../img/icono_rock.png);
}

.edm .stage-name:after {
  background-image: url(../img/icono_edm.png);
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  text-align: center;
  padding: 2rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.bg {
  background-color: rgba(0, 0, 0, 0.75);
}
.bg li:nth-child(odd) {
  background-color: rgba(59, 59, 59, 0.2);
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery-images picture {
  height: 15rem;
  overflow: hidden;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.3);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-close {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #121314;
  color: #cd9ce6;
  display: grid;
  place-items: center;
  font-family: "Bruno Ace SC", sans-serif;
}
.btn-close:hover {
  cursor: pointer;
  background-color: #dddfe1;
  color: #652087;
  transition: background-color 0.5s ease;
}

.passes {
  background-color: #000000;
  padding: 5rem 0;
}
.passes h3 {
  text-align: center;
}
@media (min-width: 768px) {
  .passes .passes-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass {
  margin-bottom: 5rem;
}
.pass:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .pass {
    margin-bottom: 0;
  }
}
.pass-header, .pass-footer {
  padding: 0.5rem 0;
  text-align: center;
}
.pass-header p, .pass-footer p {
  font-family: "Bruno Ace SC", sans-serif;
  margin: 0;
}
.pass-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}
.pass-body ul li,
.pass-body ul :nth-child(odd) {
  background-color: rgba(0, 0, 0, 0.85);
  padding: 1rem;
  border-bottom: 0.1rem solid #202020;
}
.pass-footer {
  background-color: #121314;
}
.pass-footer p {
  font-size: 2.4rem;
}

.basic .pass-header {
  border-radius: 2.5rem 2.5rem 0 0;
  background: linear-gradient(262deg, rgb(72, 72, 72) 0%, rgb(36, 36, 36) 50%, rgb(30, 30, 30) 100%);
}

.premium .pass-header {
  border-radius: 2.5rem 2.5rem 0 0;
  background: linear-gradient(262deg, rgb(123, 79, 255) 0%, rgb(62, 37, 138) 50%, rgb(15, 1, 59) 100%);
}

.footer {
  background: linear-gradient(262deg, rgb(24, 24, 24) 0%, rgb(19, 19, 19) 50%, rgb(0, 0, 0) 100%);
  padding: 1rem;
}
.footer p {
  margin: 0;
  text-align: center;
}
.footer p span {
  font-family: "Bruno Ace SC", sans-serif;
}